
#threemain{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
background:repeating-linear-gradient(rgb(236, 236, 159),rgb(247, 241, 241)) ;
width: 100vw;
height: 100vh;
/* margin: 1rem; */
gap: 2.5rem;
}
#threeh1{
    font-size: 26px;
    width: 90vw;
}
#threeimg{
width: 70vw;
height: 50vh;
display: grid;
grid-template-columns: 1fr 1fr 1fr 1fr;
align-items: center;
gap: 10px;
justify-content: center;
}
#threep{
    width: 90vw;
}
#image1{
    background-image: url('../images/four/four1.jpg');
background-size: cover;
}
#image2{
    background-image: url('../images/four/four2.jpg');
    background-size: cover;
}
#image3{
    background-image: url('../images/four/four3.jpg');
    background-size: cover;
}
#image4{
    background-image: url('../images/four/four4.jpg');
    background-size: cover;
}
#image5{
    background-image: url('../images/four/four5.jpg');
    background-size: cover;
}
#image6{
    background-image: url('../images/four/four6.jpg');
    background-size: cover;
}
#image7{
    background-image: url('../images/four/four7.jpg');
    background-size: cover;
}
#image8{
    background-image: url('../images/four/four8.jpg');
    background-size: cover;
}
.images{
    width: 300px;
    height: 150px;
    /* background-color: red; */
}
#threehome{
    position: absolute;
    top: 5px;
    left: 10px;
    text-decoration: none;
    background-color: rgb(252, 71, 71);
    padding: 5px;
    border-radius: 5px;
    color: white;
}
#threehome:hover{
    background-color: rgb(247, 12, 12);
}
@media screen and (max-width:805px) {
    #threeimg{
        grid-template-columns: 1fr 1fr;
        gap: 5px;

    }
    #threeh1{
        font-size: 22px;
    }
    #threep{
        font-size: 12px;
    }
}
#threehome:hover{
    background-color: rgb(247, 12, 12);
}
@media screen and (max-width:600px) {
    #threeimg{
        grid-template-columns: 1fr;
        gap: 5px;

    }
    #threeh1{
        font-size: 22px;
    }
    #threep{
        font-size: 12px;
    }
    .images{
        width: 450px;
        height: 200px;
    }
}