*{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin: 0px;
    padding: 0px;
    user-select: none;
}
#homenav{
/* background-color: bisque; */
display: flex;
flex-direction: row;
/* background-color: rosybrown; */

}
#homemain{
    background-color: rgb(99, 94, 94);
    background-image: url('../images/home/homein.jpg');
    width: 100vw;
    height: 100vh;
    background-size: cover;
}
#homenavh1span{
    color: rgb(250, 115, 24);
}
#homenavh1{
    margin: 0.3rem 3.5rem;
    flex: 1;
    color: white;
    text-align: start;
}
.homenavlink{
    margin: 0.5rem ;
    text-align: center;
    flex:2.5 ;
    -ms-flex-align: end;
    /* background-color: blue; */
    text-align: end;
}
.navlink{
    text-align: center;
    text-decoration: none;
    padding: 20px 5px;
    color: white;
    margin-right: 0.4rem;
}

#homenavbutton{
    background-color: rgb(250, 115, 24);
    color: white;
    border: none;
    padding: 7px;
    text-align: center;
    border-radius: 3px;
    font-weight: 700;
    font-size: 10px;
}
.navlink:hover{
 color: orange;
}
#homeparamain{
    /* background-color: rosybrown; */
    display: flex;
    flex-direction: column;
    margin-left: 3.7rem;
    margin-top: 3rem;
    width: 45vw;
    height: 40vh;
    color: white;
}
#homeparah12{
    color: rgb(235, 110, 7);
    margin-top: 10px;
    font-weight: bold;
    margin-bottom: 10px;
}
#homeparabtn{
    background-color:rgb(235, 110, 7) ;
    color: white;
    border: none;
    padding: 5px 15px;
    border-radius: 3px;
    width: 100px;
    margin-top: 2rem;
}
#homeparah11{
    font-weight:bold;
}
#homeparabtn:hover{
  transition: 0.5s linearS;
  transform: scaleX(1.1);
}
#homeboxmain{
    display: flex;
    flex-direction: row;
    /* background-color: blue; */
    margin-top: 12.5rem;
    background-color: red;
    width: 35rem;
}
.homebox1{
background-color: rgb(20, 19, 19);
color: white;
padding: 10px;
}
.homebox2{
    background-color: rgb(192, 185, 185);
    color: rgb(19, 18, 18);
    padding: 10px;  
}
.homebox3{
    background-color: rgb(243, 239, 239);
    color: rgb(20, 20, 20);
    padding: 10px;  
}
.homeboxh1{
    font-size: 19px;
    font-weight: 700;
}
.homeboxp1{
    font-size: 11px;
    font-weight:lighter;
}
#homeboxa1{
 color: white;   
}
#homeboxa2{
 color: rgb(17, 16, 16);   
}
#homeboxa3{
 color: rgb(15, 15, 15);   
}
#homebuttonlink{
    position: absolute;
    width: 3rem;
    height: 2rem;
    top: 10px;
    right: 20px;
    color: white;
    border: none;
    border-radius: 5px;
    background-color: orangered;
    display: none;
}
@media screen and (max-width:805px){
    #homenav{
        flex-direction: column;

    }
    
#homebuttonlink{
    display: block;
}
    .homenavlink{
        display: flex;
        flex-direction: column; 
        align-items: center;
        /* display: none; */
        /* visibility: hidden; */
    }
    #homemain{
        width: 100vw;
        height: 153.6vh;
        background-size: cover;
    }
}

@media screen and (max-width:600px){
    #homeboxmain{
        flex-direction: column;
        text-align: center;
       justify-content: center;
        width: 100vw;
        height: 20vh;
    }
    #homemain{
        width: 100vw;
        height: 165vh;
        background-size: cover;
    }
}