#finalmain{
display: flex;
flex-direction: row;
align-items: center;
background:repeating-linear-gradient(rgb(241, 241, 11),rgb(247, 241, 241)) ;
justify-content: center;
width: 100vw;
gap: 50px;
height: 100vh;
}
#finalimag{
background-image: url('../images/contact/contactas.jpg');
width: 30vw;
height: 72vh;
background-size: cover;
display: flex;
align-items: center;
justify-content: center;
color: rgb(247, 5, 85);
font-size: 30px;
border-radius: 10px;
}
#finalform{
display: flex;
flex-direction: column;
color: orangered;
width: 40vw;
height: 72vh;
}
.finalformh1{
    margin-bottom: 1rem;
    border-bottom: solid rgb(197, 189, 189) 1px;

}

#finalname{
padding: 5px;
border: solid rgb(177, 162, 162) 1px;
}

#finalemail{
padding: 5px;
border: solid rgb(177, 162, 162) 1px;
}

#finalcontact{
padding: 5px;
border: solid rgb(177, 162, 162) 1px;
}

#finalbtn{
color: white;
width: 6rem;
border: none;
padding: 5px;
background-color: orangered;
font-weight: 200;
}
#finalhome{
    position: absolute;
    top: 5px;
    left: 9px;
    background-color: rgb(248, 9, 9);
    border: none;
    text-decoration: none;
    color: white;
    padding: 5px;
    border-radius: 5px;
}
#finalhome:hover{
  background-color: rgb(224, 54, 54);
}

@media screen and (max-width:805px){
    #finalmain{
       width: 100vw;
       height: 150vh;
        flex-direction: column;
    }
    #finalimag{

        margin-top: 2rem;
        width: 80vw;
    }
    #finalform{
        width: 80vw;
    }
}


@media screen and (max-width:600px){
    #finalmain{
        width: 100vw;
        height: 150vh;
         flex-direction: column;
     }
     #finalimag{
 
         margin-top: 2rem;
         width: 80vw;
     }
     #finalform{
         width: 80vw;
     }
}