
#secondmain{
display: flex;
flex-direction: column;
width: 100vw;
height: 100vh;
align-items: center;
justify-content: center;
gap: 70px;
background:repeating-linear-gradient(rgb(241, 241, 11),rgb(247, 241, 241)) ;
}
#secondh1{
    margin-top: 2rem;
font-size: 30px;
color: rgb(78, 38, 38);
}
#secondp{
margin: 0px 5rem;
font-weight: 100;
font-size: 14px;
color: rgb(78, 38, 38);
}
#secondimg{
background-image: url('../images/second/secondin.jpg');
width: 450px;
height: 280px;
background-size: cover;
}
#secondlink{
    background-color: rgb(255, 51, 0);
    border: none;
    text-decoration: none;
    position: absolute;
    color: white;
    top: 5px;
    left: 10px;
    padding: 5px;
    border-radius: 5px;
}
#secondlink:hover{
    background-color: rgb(10, 241, 22);
    color: rgb(214, 17, 132);
}
@media screen and (max-width:575px) {
 
#secondh1{
    font-size: 25px;
    }   
    #secondlink{
        margin-bottom: 1rem;

    }
}